<template>
  <div class="pt88">
    <head-bar v-bind:head="headdata" @headfun='skip()'></head-bar>
    <div class="heados">
      <div class="step-cont">
        <div class="step-title">Step 4/5</div>
        <div class="step-label">你的运动量 ?</div>
        <div class="step-form">
          <div class="step-item"
            @click="selectThis(index)"
            :class="item.checked?'gradient-back active':''"
            v-for="(item,index) in active"
            :key="index">
            {{item.title}}
          </div>
        </div>
      </div>
      <div class="step-next gradient-back" @click="nextStep()">下一步</div>
    </div>
  </div>
</template>

<script>
import headBar from '@/components/common/head';

export default {
  components: {
    headBar
  },
  name: 'step4',
  data () {
    return {
      headdata: {
        text: '',
        src: require('@/assets/images/icon-30.png'),
        span: 'SKIP'
      },
      frequency: '',
      active: [
        {title: '无运动', checked: false},
        {title: '很少运动', checked: false},
        {title: '正常', checked: false},
        {title: '很爱运动', checked: false}
      ]
    }
  },
  created(){
    this.initPage();
  },
  methods: {
    initPage(){

    },
    skip(){
      console.log('skip');
    },
    selectThis(index){
      this.active.forEach(item=>{
        item.checked = false;
      })
      this.active[index].checked = true;
      this.frequency = this.active[index].title;
    },
    nextStep(){
      var _this = this;
      var msg = '';
      if(_this.frequency.length==0){
        msg = 'Please select your active index'
      }
      if(msg.length>0){
        _this.$toast(msg)
      }else{
        var data = _this.$route.params;
        data['active'] = _this.frequency;
        data['step'] = 4;
        _this.DB.put(data, function(res){
          _this.$router.push({
            path: '/step5',
            name: 'step5',
            params: data
          })
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.step-form{
  padding: 0.5rem 0.5rem 0;
}
.step-item{
  width: 100%;
  height: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  color: #6d819c;
  border: 0.04rem solid #e9e9e9;
  border-radius: 1.5rem;
  margin-bottom: 0.5rem;
}
.active{
  color: #fff;
  border-color: #fff;
}
</style>



